import * as React from 'react'
import styled from 'styled-components'

import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Jumbotron from 'react-bootstrap/Jumbotron'
import ListGroup from 'react-bootstrap/ListGroup'
import ListGroupItem from 'react-bootstrap/ListGroupItem'
import SwapiService, { Character } from '../services/SwapiService'
import { Error, StyledSpinner } from './Styles'

const StyledJumbotron = styled(Jumbotron)`
  padding: 0;
`

const StyledListGroupItem = styled(ListGroupItem)`
  border-color: #839496;
  cursor: pointer;
  :hover {
    opacity: 0.8;
  }
`

const SpinnerWrapper = styled.div`
  padding-top: 2rem;
  padding-bottom: 2rem;
  margin: auto;
  width: 100%;
`

const PaddedStyledSpinner = styled(StyledSpinner)`
  margin-top: 2rem;
  margin-bottom: 2rem;
`

interface Props {
    onCharacterSelected: (id: string) => void
}

interface State {
    characters: Character[],
    loading: boolean
    error: boolean
}

export default class ItemList extends React.Component<Props, State> {
    state = {
        characters: [] as Character[],
        loading: true,
        error: false,
    }

    private swapiService = new SwapiService()

    componentDidMount(): void {
        this.swapiService.getAllCharacters()
            .then(characters => {
                this.setState({ characters, loading: false, error: false })
            })
    }

    render() {
        const { error, loading } = this.state
        let content
        if (error) {
            content = <Row>
                <Error>Something went wrong..</Error>
            </Row>
        } else if (loading) {
            content = <Row>
                <PaddedStyledSpinner animation="border"/>
            </Row>
        } else {
            content = (<ListGroup>
                { this.state.characters.map(character => {
                    return (
                        <StyledListGroupItem key={ character.id } onClick={ () => this.props.onCharacterSelected(character.id) }>
                            { character.name }
                        </StyledListGroupItem>
                    )
                }) }
            </ListGroup>)
        }

        return (<StyledJumbotron>{ content }</StyledJumbotron>)
    }

}
