import * as React from 'react'
import Header from './Header'
import RandomPlanet from './RandomPlanet'
import ItemList from './ItemList'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'

import ItemDetails from './ItemDetails'

interface State {
    randomPlanet: boolean,
    selectedCharacterId: string | null,
}

export default class App extends React.Component<{}, State> {
    state = {
        randomPlanet: true,
        selectedCharacterId: null as null
    }

    onCharacterSelected = (id: string) => {
        this.setState({ selectedCharacterId: id })
    }

    toggleRandomPlanet = () => {
        this.setState(state => {
            return { randomPlanet: !state.randomPlanet }
        })
    }

    render() {
        let randomPlanet = null
        if (this.state.randomPlanet) {
            randomPlanet = <RandomPlanet/>
        }

        return (
            <React.Fragment>
                <Header/>
                <Container>
                    <Button onClick={ this.toggleRandomPlanet }>Toggle random planet</Button>
                </Container>
                { randomPlanet }
                <Container>
                    <Row>
                        <Col lg={ 4 } md={ 6 } xs={ 12 }>
                            <ItemList onCharacterSelected={ this.onCharacterSelected }/>
                        </Col>
                        <Col lg={ 8 } md={ 6 } xs={ 12 }>
                            <ItemDetails characterId={ this.state.selectedCharacterId }/>
                        </Col>
                    </Row>
                </Container>
            </React.Fragment>
        )
    }
}

