import * as React from 'react'
import { Error, StyledSpinner } from './Styles'
import Row from 'react-bootstrap/Row'
import ListGroup from 'react-bootstrap/ListGroup'
import SwapiService, { Character } from '../services/SwapiService'
import styled from 'styled-components'
import ListGroupItem from 'react-bootstrap/ListGroupItem'
import Jumbotron from 'react-bootstrap/Jumbotron'

const StyledJumbotron = styled(Jumbotron)`
  padding: 1rem;
`

const StyledHeader = styled.h2``

const RowNoMargin = styled(Row)`
  margin-left: 0;
  margin-right: 0;
`

const StyledListGroupItem = styled(ListGroupItem)`
  border-color: #839496;
  border-bottom: none;
  border-left: none;
  border-right: none;
  :first-child {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
`

const ItemTitle = styled.span`
  font-weight: bold;
  margin-right: 8px;
`

const ItemValue = styled.span`
  font-weight: normal;
  color: #B58900;
`

interface Props {
    characterId: string | null
}

interface State {
    character: Character | null
    loading: boolean
    error: boolean
}

export default class ItemDetails extends React.Component<Props, State> {
    state = {
        loading: false,
        error: false,
        character: null as Character,
    }

    private swapiService = new SwapiService()

    componentDidMount() {
        this.updateCharacter()
    }

    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<State>, snapshot?: any): void {
        if (this.props.characterId !== prevProps.characterId) {
            this.updateCharacter()
        }
    }

    private updateCharacter() {
        const { characterId } = this.props
        if (!characterId) {
            return
        }

        this.setState({ loading: true })

        this.swapiService.getCharacter(characterId)
            .then(character => this.setState({ character, loading: false, error: false }))
    }

    render() {
        const { error, loading } = this.state
        let content
        if (error) {
            content = <Row>
                <Error>Something went wrong..</Error>
            </Row>
        } else if (loading) {
            content = <Row>
                <StyledSpinner animation="border"/>
            </Row>
        } else if (!this.state.character) {
            content = <Row>
                <Error>Select character..</Error>
            </Row>
        } else {
            content = (
                <RowNoMargin>
                    <ListGroup>
                        <StyledHeader>{ this.state.character.name }</StyledHeader>
                        <StyledListGroupItem>
                            <ItemTitle>Population:</ItemTitle>
                            <ItemValue>123</ItemValue>
                        </StyledListGroupItem>
                        <StyledListGroupItem>
                            <ItemTitle>Population:</ItemTitle>
                            <ItemValue>123 123 123 123 123 123 123 123 123 123 123 123 123</ItemValue>
                        </StyledListGroupItem>
                    </ListGroup>
                </RowNoMargin>
            )
        }

        return (<StyledJumbotron>{ content }</StyledJumbotron>)
    }
}
